@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Regular"), url("../fonts/montserrat_regular.woff2") format("woff2"), url("../fonts/montserrat_regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat Medium";
  src: local("Montserrat Medium"), url("../fonts/montserrat_medium.woff2") format("woff2"), url("../fonts/montserrat_medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat SemiBold";
  src: local("Montserrat SemiBold"), url("../fonts/montserrat_semibold.woff2") format("woff2"), url("../fonts/montserrat_semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat Bold";
  src: local("Montserrat Bold"), url("../fonts/montserrat_bold.woff2") format("woff2"), url("../fonts/montserrat_bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
.visually-hidden:not(:active):not(:focus),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  overflow: hidden;
}
body {
  position: relative;
  min-width: 375px;
  margin: 0 auto;
  padding: 0;
  color: #30353e;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  line-height: 22px;
  background-color: #ffffff;
}
.header {
  position: relative;
  display: flex;
  padding: 12px;
  padding-top: 10px;
  padding-bottom: 9px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.06);
}
.header__logo {
  width: 116px;
  margin-left: 3px;
  text-transform: lowercase;
  text-decoration: none;
}
.header__logo-first-line {
  margin: 0;
  color: #30353e;
  font-family: "Montserrat Bold", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
}
.header__logo-first-line span:last-child {
  color: #2c78f6;
}
.header__logo-second-line {
  margin: 0;
  color: #909aab;
  font-family: "Montserrat SemiBold", "Arial", sans-serif;
  font-size: 9px;
  line-height: 9px;
  font-weight: normal;
  font-style: normal;
}
.header__searching-form {
  display: none;
}
.header__searching-toggle {
  width: 30px;
  height: 30px;
  margin-left: auto;
  margin-top: 1px;
  margin-right: 19px;
  border: none;
  background: url("../img/search-icon.png") center center no-repeat;
  background-size: 23px 24px;
}
.header__user-menu-button {
  display: block;
  width: 30px;
  height: 30px;
  background: url("../img/login-icon.png") center center no-repeat;
  background-size: 24px 24px;
}
.header__user-menu-button span {
  display: none;
}
.header__user-menu {
  position: absolute;
  right: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}
.header__user-menu-link {
  display: block;
  text-decoration: none;
}
@media (min-width: 1024px) {
  .header {
    padding: 57px;
    padding-top: 16px;
    padding-bottom: 16px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.06);
  }
  .header__logo {
    width: 120px;
    margin-left: 3px;
    margin-top: 4px;
    letter-spacing: 0.4px;
    text-transform: lowercase;
    text-decoration: none;
  }
  .header__logo-second-line {
    margin-left: 1px;
    letter-spacing: 0.15px;
  }
  .header__searching-form {
    display: block;
    margin-left: auto;
    margin-right: 225px;
  }
  .header__searching-field {
    display: block;
    box-sizing: border-box;
    width: 513px;
    height: 38px;
    padding-left: 38px;
    border: 1px solid #eaecef;
    border-radius: 5px;
    font-family: "Montserrat Medium", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 22px;
    color: #30353e;
    background: url("../img/search-icon-2.png") 10px 9px no-repeat;
  }
  .header__searching-toggle {
    display: none;
  }
  .header__user-menu-button {
    margin-left: auto;
    margin-top: 2px;
    margin-right: 3px;
    width: 33px;
    height: 33px;
    background: url("../img/login-icon-desktop.png") center center no-repeat;
    background-size: 33px 33px;
  }
}
@media (min-width: 1800px) {
