}
.form-section__phone-field {
  display: block;
  width: 100%;
  height: 19.5px;
  margin-top: -3px;
  margin-bottom: 35px;
  padding-top: 7px;
  padding-bottom: 6px;
  border: none;
  border-bottom: 2px solid #61656b;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 16px;
  color: #30353e;
  background-color: transparent;
}
.form-section__phone-checked {
  display: none;
  position: relative;
}
.form-section__phone-field + .form-section__phone-checked::after {
  content: "";
  position: absolute;
  top: -52px;
  right: 0;
  width: 10px;
  border: 1px solid #45c781;
  border-top: 1px solid #45c781;
  border-top-right-radius: 1px;
  border-bottom: 1px solid #45c781;
  border-bottom-right-radius: 1px;
  border-bottom-left-radius: 1px;
  background-color: #45c781;
  transform: rotate(-45deg);
}
.form-section__phone-field + .form-section__phone-checked::before {
  content: "";
  position: absolute;
  top: -50px;
  right: 8px;
  width: 5px;
  border: 1px solid #45c781;
  border-top: 1px solid #45c781;
  border-top-left-radius: 1px;
  border-bottom: 1px solid #45c781;
  border-bottom-left-radius: 1px;
  border-bottom-right-radius: 1px;
  background-color: #45c781;
  transform: rotate(45deg);
}
