.slider {
  position: relative;
  box-sizing: border-box;
  width: 56px;
  height: 21px;
  background-color: transparent;
}
.slider::before {
  content: "";
  position: absolute;
  margin-top: 3px;
  width: 8px;
  height: 8px;
  top: 6px;
  left: 0;
  background-color: rgba(44, 120, 246, 0.6);
  box-shadow: 16px 0 0 0 rgba(44, 120, 246, 0.6), 32px 0 0 0 rgba(44, 120, 246, 0.6), 48px 0 0 0 rgba(44, 120, 246, 0.6);
  border-radius: 50%;
}
input[type="range"] {
  background-color: transparent;
  -webkit-appearance: none;
  width: 56px;
  margin-top: -8px;
  padding-top: 11px;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 56px;
  height: 8px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-radius: 5px;
  border: none;
}
input[type="range"]::-webkit-slider-thumb {
  height: 8px;
  width: 24px;
  border-radius: 8px;
  background: #2c78f6;
  cursor: pointer;
  border: none;
  -webkit-appearance: none;
  margin-top: 1px;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: transparent;
}
input[type="range"]::-moz-range-track {
  width: 56px;
  height: 8px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-radius: 5px;
  border: none;
}
input[type="range"]::-moz-range-thumb {
  height: 8px;
  width: 24px;
  border-radius: 8px;
  background: #2c78f6;
  cursor: pointer;
  border: none;
}
input[type="range"]::-ms-track {
  width: 56px;
  height: 8px;
  cursor: pointer;
  border: none;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 24px 0;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: transparent;
  border: none;
  border-radius: 10px;
}
input[type="range"]::-ms-fill-upper {
  background: transparent;
  border: none;
  border-radius: 10px;
}
input[type="range"]::-ms-thumb {
  height: 8px;
  width: 24px;
  border-radius: 8px;
  background: #2c78f6;
  cursor: pointer;
  border: none;
}
input[type="range"]:focus::-ms-fill-lower {
  background: transparent;
}
input[type="range"]:focus::-ms-fill-upper {
  background: transparent;
}
